<template>
    <div class='group'>
        <div class='bagroup' @mouseover='show' @mouseout="ushow">
            <svg class='brass' @mouseenter="enter('harp')" @mouseleave="leave">

                <use xlink:href="#i-harp" />

            </svg>
            <svg class='brass' @mouseenter="enter('piano')" @mouseleave="leave">

                <use xlink:href="#i-piano-" />

            </svg>

            <h2 v-if=ishover>color instruments</h2>
        </div>

    </div>
</template>
<script>
    export default {
        data() {
            return {
                player: null,
                ishover: false
            }
        },
        methods: {
            enter(mes) {
                console.log(mes)
                this.player = new Audio(`../../sound/${mes}.mp3`)
                this.player.play()
            },
            leave() {
                this.player.pause()
                this.player = null
            },
            show() {
                this.ishover = true
            },
            ushow() {
                this.ishover = false
            },
        }
    }

</script>
<style scoped="true">
    .group {
        text-align: center
    }

    .brass {
        width: 50px;
        height: 50px
    }

    .brass:hover {
        fill: #42b983;
    }

    .bagroup {
        width: 200px;
        margin: 0 auto
    }

    .bagroup:hover {

        background-color: rosybrown
    }

</style>
