<template>
    <div class='group' @mouseover='show' @mouseout="ushow">
        <div class='bgroup'>

            <svg class='b' @mouseenter="enter('cym')" @mouseleave="leave">
                <use xlink:href="#i-cym" />
            </svg>
            <svg class='b' @mouseenter="enter('triangle')" @mouseleave="leave">

                <use xlink:href="#i-tri" />

            </svg>
            <svg class='b' @mouseenter="enter('tam')" @mouseleave="leave">

                <use xlink:href="#i-tam" />

            </svg>
            <h2 v-if=ishover>percussion</h2>
        </div>

    </div>
</template>
<script>
    export default {
        data() {
            return {
                player: null,
                ishover: false
            }
        },
        methods: {
            enter(mes) {
                console.log(mes)
                this.player = new Audio(`../../sound/${mes}.mp3`)
                this.player.play()
            },
            leave() {
                this.player.pause()
                this.player = null
            },
            show() {
                this.ishover = true
            },
            ushow() {
                this.ishover = false
            },
        }
    }

</script>
<style scoped="true">
    .group {
        text-align: center
    }

    .b {
        width: 50px;
        height: 50px
    }

    .b:hover {
        fill: #42b983;
    }

    .bgroup {
        width: 200px;
        margin: 0 auto
    }

    .bgroup:hover {

        background-color: paleturquoise
    }

</style>
