<template>
    <div>
        <div position=relative>


            <flat-surface-shader class="shader" type="webgl" :light='light'>
            </flat-surface-shader>

            <div class="mask">
                <d3-test class="dchart" v-if='pi==3'></d3-test>
                <div class="info07"></div>
                <vue-lottie class="loader__new-icon" :options="params" :height="250" :width="250" @animCreated="handleAnimation"></vue-lottie>
                <h2>2007:first iPhone</h2>
                <countTo :startVal='this.ti.astart' :endVal=' this.ti.aend' :duration='800' :prefix='pref' position=absolute></countTo>

                <button class="btn" @click="show">Know more
                </button>
            </div>
        </div>
    </div>
</template>
<script>
    import D3Test from '../chart/chart2007.vue'
    import countTo from 'vue-count-to';
    import VueLottie from 'vue-lottie';
    import animationData from '../../assets/mobile.json'
    export default {
        components: {
            countTo,
            VueLottie,
            D3Test
        },
        props: ['ti', 'pi'],
        data() {
            return {

                light: {
                    ambient: '#9c1b31',
                    diffuse: '#82303e'
                },

                pref: 'Websites:',
                note: ['chrome was released in 2008', 'node.js was released in 2009'],
                clickc: 0,
                clickm: 0,
                startVal: 1,
                endVal: 1,
                gameWidth: 960,
                gameHeight: 480,
                cjs: null,
                canvas: null,
                stage: null,
                params: {
                    animationData,
                    container: null,
                    renderer: 'svg',
                    loop: true,
                    autoplay: true,
                },
            }
        },
        methods: {
            show() {

                if (this.clickm > this.note.length - 1) {
                    console.log('end')
                } else {
                    this.$noty.info(this.note[this.clickm], {
                        timeout: 5000,
                        progressBar: true,
                        container: '.info07'
                    })
                    this.clickm += 1
                }

            },
            handleAnimation(instance) {
                this.animationInstance = instance;
                this.animationInstance.setSpeed(1);
            },
        }
    }

</script>
<style scoped="true">
    .shader {
        width: 100%;
        height: 100vh;
    }

    .info {
        width: 20%;
    }

    .mask {
        width: 100%;
        height: 100vh;
        position: absolute;
        background-color: rgba(0, 0, 0, 0.6);
        top: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        color: rgba(255, 255, 255, 0.9)
    }

    .dchart {
        position: absolute;
        left: 150px;
        top: 0px;
    }

    .info07 {
        position: absolute;
        top: 10%;
        left: 0%
    }

    .btn {
        width: 120px;
        height: 40px;
        border-width: 0px;
        border-radius: 3px;
        background: #1E90FF;
        cursor: pointer;
        outline: none;
        color: white;
        font-size: 17px;
    }

    .btn:hover {
        background: #5599FF;
    }

</style>
