<template>
    <div class='group' @mouseover='show' @mouseout="ushow">
        <div class='bgroup' @mouseover='show' @mouseout="ushow">
            <svg class='b' >

                <use xlink:href="#i-harp" />

            </svg>
            <svg class='b'>

                <use xlink:href="#i-piano-" />

            </svg>
        </div>

    </div>
</template>
<script>
    export default {
        data() {
            return {
                player: null,
                ishover: false
            }
        },
        methods: {
            enter(mes) {
                console.log(mes)
                this.player = new Audio(`../../sound/${mes}.mp3`)
                this.player.play()
            },
            leave() {
                this.player.pause()
                this.player = null
            },
            show() {
                this.ishover = true
            },
            ushow() {
                this.ishover = false
            },
        }
    }

</script>
<style scoped="true">
    .group {
        text-align: center
    }

    .b {

        position: relative;
        top: 15px;
        width: 50px;
        height: 50px;
        transform: rotateZ(60deg);
    }

    .b:hover {
        fill: #42b983;
    }

    .bgroup {
        width: 250px;

        height: 80px;
        transform: rotateZ(-60deg);

        background: linear-gradient(-60deg, transparent 45px, #ac6eb8 0) right, linear-gradient(60deg, transparent 45px, #ac6eb8 0) left;

        background-size: 60% 100%;

        background-repeat: no-repeat;

        border-radius: 100% 100% 0 0;
    }

