<template>
    <div class="csstransforms">
        <button class="cn-button" id="cn-button" @click='handler'>{{ message }}</button>
        <div class="cn-wrapper" id="cn-wrapper" :class="{'opened-nav':open}">
            <ul>
                <li><span>
                    </span></li>
                <li><span>
                        <router-link to="/demo3/p2"> <svg id='j1'>
                                <use xlink:href="#i-conductor" />
                            </svg></router-link>
                    </span></li>
                <li><span>
                        <router-link to="/demo3/p1"><svg id='j2'>

                                <use xlink:href="#i-doublebass" />

                            </svg></router-link>
                    </span></li>
                <li><span>
                        <router-link to="/demo3/jaze"><svg id='j3'>

                                <use xlink:href="#i-choir-" />

                            </svg></router-link>
                    </span></li>




                <li><span>

                    </span></li>
            </ul>
        </div>
        <div id="cn-overlay" class="cn-overlay" :class="{'on-overlay':open}"></div>
    </div>

</template>
<script>
    export default {
        data() {
            return {
                open: false,
                message: '+'
            }
        },
        methods: {
            handler() {
                console.log(this.open)
                if (!this.open) {
                    this.openNav();
                } else {
                    this.closeNav();
                }
            },
            openNav() {
                this.open = true;
                this.message = '-'

            },
            closeNav() {
                this.open = false;
                this.message = '+'

            }
        }
    }

</script>
<style scoped="true">
    .csstransforms .cn-wrapper {
        font-size: 1em;
        width: 26em;
        height: 26em;
        overflow: hidden;
        position: fixed;
        z-index: 10;
        bottom: -13em;
        left: 50%;
        border-radius: 50%;
        margin-left: -13em;
        transform: scale(0.1);
        transition: all .3s ease;
    }

    /* class applied to the container via JavaScript that will scale the navigation up */
    .csstransforms .opened-nav {
        border-radius: 50%;
        transform: scale(1);
    }

    .cn-button {
        border: none;
        background: none;
        color: white;
        text-align: Center;
        font-size: 1.5em;
        padding-bottom: 1em;
        height: 3.5em;
        width: 3.5em;
        background-color: #111;
        position: fixed;
        left: 50%;
        margin-left: -1.75em;
        bottom: -1.75em;
        border-radius: 50%;
        cursor: pointer;
        z-index: 11
    }

    .cn-button:hover,
    .cn-button:active,
    .cn-button:focus {
        background-color: #222;
    }

    .cn-overlay {
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.6);
        position: fixed;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        opacity: 0;
        transition: all .3s ease;
        z-index: 2;
        pointer-events: none;
    }

    /* Class added to the overlay via JavaScript to show it when navigation is open */
    .cn-overlay.on-overlay {
        pointer-events: auto;
        opacity: 1;
    }

    .csstransforms .cn-wrapper li {
        position: absolute;
        font-size: 1.5em;
        width: 20em;
        height: 11em;
        transform-origin: 100% 100%;
        overflow: hidden;
        left: 0%;
        top: 0%;
        margin-top: -1.3em;
        margin-left: -11.5em;
        transition: border .3s ease;
    }

    .csstransforms .cn-wrapper li span {
        display: block;
        font-size: 1.18em;
        height: 14em;
        width: 14em;
        position: absolute;
        bottom: -7em;
        right: -7.5em;
        border-radius: 50%;
        text-decoration: none;
        color: #fff;
        padding-top: 1.8em;
        text-align: center;
        transform: skew(-50deg) rotate(-70deg) scale(1);
        transition: opacity 0.3s, color 0.3s;
    }

    /* for a central angle x, the list items must be skewed by 90-x degrees in our case x=40deg so skew angle is 50deg items should be rotated by x, minus (sum of angles - 180)2s (for this demo) */
    .csstransforms .cn-wrapper li:first-child {
        transform: rotate(-15deg) skew(50deg);
    }

    .csstransforms .cn-wrapper li:nth-child(2) {
        transform: rotate(25deg) skew(50deg);
    }

    .csstransforms .cn-wrapper li:nth-child(3) {
        transform: rotate(65deg) skew(50deg)
    }

    .csstransforms .cn-wrapper li:nth-child(4) {
        transform: rotate(105deg) skew(50deg);
    }

    .csstransforms .cn-wrapper li:nth-child(5) {
        transform: rotate(145deg) skew(50deg);
    }

    .csstransforms .cn-wrapper li:nth-child(odd) span {
        background-color: #a11313;
        background-color: hsla(0, 88%, 63%, 1);
    }

    .csstransforms .cn-wrapper li:nth-child(even) span {
        background-color: #a61414;
        background-color: hsla(0, 88%, 65%, 1);
    }

    /* active style */
    .csstransforms .cn-wrapper li.active span {
        background-color: #b31515;
        background-color: hsla(0, 88%, 70%, 1);
    }

    /* hover style */
    .csstransforms .cn-wrapper li:not(.active) span:hover,
    .csstransforms .cn-wrapper li:not(.active) span:active,
    .csstransforms .cn-wrapper li:not(.active) span:focus {
        background-color: #b31515;
        background-color: hsla(0, 88%, 70%, 1);
    }

    #j1 {
        position: relative;
        top: 50px;
        left: -25px;
        width: 50px;
        height: 50px;
        opacity: 0.7;
    }

    #j2 {
        position: relative;
        top: 50px;
        left: -20px;
        width: 50px;
        height: 50px;
        opacity: 0.7;
    }

    #j3 {
        position: relative;
        top: 50px;
        left: -25px;
        width: 50px;
        height: 50px;
        opacity: 0.7;
    }

</style>
