<template>
    <div class='group' @mouseover='show' @mouseout="ushow">
        <div class='bgroup'>

            <svg class='beat'>
                <use xlink:href="#i-cym" />
            </svg>
            <svg class='beat'>

                <use xlink:href="#i-tri" />

            </svg>
            <svg class='beat'>

                <use xlink:href="#i-tam" />

            </svg>
        </div>

    </div>
</template>
<script>
    export default {
        data() {
            return {
                player: null,
                ishover: false
            }
        },
        methods: {
            enter(mes) {
                console.log(mes)
                this.player = new Audio(`../../sound/${mes}.mp3`)
                this.player.play()
            },
            leave() {
                this.player.pause()
                this.player = null
            },
            show() {
                this.ishover = true
            },
            ushow() {
                this.ishover = false
            },
        }
    }

</script>
<style scoped="true">
    .bgroup {
        width: 300px;

        height: 80px;
        margin: 0px;

        background: #58a;

        background: linear-gradient(-60deg, transparent 45px, #3282b8 0) right,
            linear-gradient(60deg, transparent 45px, #3282b8 0) left;

        background-size: 60% 100%;

        background-repeat: no-repeat;
        border-radius: 100% 100% 0 0;
    }

    .beat {
        position: relative;
        top: 15px;
        width: 50px;
        height: 50px
    }

    .beat:hover {
        fill: #42b983;
    }


