<template>
    <div>
        <div class='group' @mouseover='show' @mouseout="ushow">
            <div class='bgroup'>

                <svg class='beat'>
                    <use xlink:href="#i-drummer-" />
                </svg>
                <svg class='beat'>

                    <use xlink:href="#i-piano-" />

                </svg>
                <svg class='beat'>

                    <use xlink:href="#i-doublebass" />

                </svg>
            </div>
        </div>
        <div class='group' @mouseover='show' @mouseout="ushow">
            <div class='bgroup'>

                <svg class='beat' @mouseenter="enter('saxophone')" @mouseleave="leave">

                    <use xlink:href="#i-saxophone" />
                </svg>
                <svg class='beat' @mouseenter="enter('trombone')" @mouseleave="leave">

                    <use xlink:href="#i-trumpet-" />

                </svg>
            </div>
        </div>
    </div>

</template>
<script>
    export default {
        data() {
            return {
                player: null,
                ishover: false
            }
        },
        methods: {
            enter(mes) {
                console.log(mes)
                this.player = new Audio(`../../sound/${mes}.mp3`)
                this.player.play()
            },
            leave() {
                this.player.pause()
                this.player = null
            },
            show() {
                this.ishover = true
            },
            ushow() {
                this.ishover = false
            },
        }
    }

</script>
<style scoped="true">
    .group {
        text-align: center;
        border-radius: 5px
    }

    .beat {
        width: 50px;
        height: 50px;
        transform: skew(20deg);
    }

    .beat:hover {
        fill: #42b983;
    }

    .bgroup {
        margin: 0 auto
    }

</style>
