import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Svgs from './views/Svg.vue'
import About from './views/About.vue'
import d3test from './views/d3test.vue'
import building from './views/building.vue'
import P1 from './views/p1'
import P2 from './views/p2'
import P3 from './views/p3'
import orce from './views/orcensem'
import jaze from './views/jazensem'


Vue.use(Router)

export default new Router({
    mode: 'history',
    base: process.env.BASE_URL,
    routes: [
        {
            path: '/',
            name: 'home',
            component: Home
    },
        {
            path: '/demo2',
            name: 'demo2',
            // route level code-splitting
            // this generates a separate chunk (about.[hash].js) for this route
            // which is lazy-loaded when the route is visited.
            //redirect: '/about/pageone',
            component: About,

    },
        {
            path: '/demo3',
            name: 'demo3',
            component: Svgs,
            children: [
                {
                    // 当 /user/:id/profile 匹配成功，
                    // UserProfile 会被渲染在 User 的 <router-view> 中
                    path: 'p1',
                    component: P1
        },
                {
                    // 当 /user/:id/posts 匹配成功
                    // UserPosts 会被渲染在 User 的 <router-view> 中
                    path: 'p2',
                    component: P2
        },
                {
                    // 当 /user/:id/posts 匹配成功
                    // UserPosts 会被渲染在 User 的 <router-view> 中
                    path: 'p3',
                    component: P3
        },
                {
                    // 当 /user/:id/posts 匹配成功
                    // UserPosts 会被渲染在 User 的 <router-view> 中
                    path: 'orce',
                    component: orce
        },
                {
                    // 当 /user/:id/posts 匹配成功
                    // UserPosts 会被渲染在 User 的 <router-view> 中
                    path: 'jaze',
                    component: jaze
        }
      ]
    },
        {
            path: '/demo4',
            name: 'demo4',
            // route level code-splitting
            // this generates a separate chunk (about.[hash].js) for this route
            // which is lazy-loaded when the route is visited.
            //redirect: '/about/pageone',
            component: d3test,

    },
        {
            path: '/demo1',
            name: 'demo1',
            // route level code-splitting
            // this generates a separate chunk (about.[hash].js) for this route
            // which is lazy-loaded when the route is visited.
            //redirect: '/about/pageone',
            component: building,

    },

  ]
})
