<template>
    <div class='group' @mouseover='show' @mouseout="ushow">
        <div class='bgroup'>

            <svg class='beat'>

                <use xlink:href="#i-piano-" />

            </svg>

        </div>
        <div class='bgroup'>

            <svg class='beat'>
                <use xlink:href="#i-violin-" />
            </svg>

            <svg class='beat'>

                <use xlink:href="#i-violoncello" />

            </svg>
        </div>

    </div>
</template>
<script>
    export default {
        data() {
            return {
                player: null,
                ishover: false
            }
        },
        methods: {
            enter(mes) {
                console.log(mes)
                this.player = new Audio(`../../sound/${mes}.mp3`)
                this.player.play()
            },
            leave() {
                this.player.pause()
                this.player = null
            },
            show() {
                this.ishover = true
            },
            ushow() {
                this.ishover = false
            },
        }
    }

</script>
<style scoped="true">
    .group {
        text-align: center;
        border-radius: 5px;
        width: 300px;

        height: 120px;

        transform: skew(30deg);

        background-size: 60% 100%;

        background-repeat: no-repeat;
        background-color: rosybrown;
    }

    .beat {
        width: 50px;
        height: 50px;
        transform: skew(-30deg);
    }

    .beat:hover {
        fill: #42b983;
    }

    .bgroup {
        margin: 0 auto
    }

    .bgroup:hover {

        background-color: lightblue
    }

</style>
