<template>
    <svg width="400" height="250"></svg>
</template>

<script>
    import * as d3 from 'd3';
    const data = [
        ["2002: 38760373", 81],
        ["2001: 29254370", 71],
        ["2000: 17087182", 53],
        ["1999: 3177453", 23],
        ["1998: 2410067", 20],
        ["1997: 1117255", 14],
        ["1996: 257601", 7],
        ["1995: 23500", 2],
    ];
    export default {
        mounted: function() {
            // this.#el - is the root element in <template>
            // in this case it is <svg> tag
            var svg = d3.select(this.$el);
            const color = d3.scaleOrdinal(d3.schemeCategory10);
            svg.selectAll('circle').data(data).enter()
                .append('circle')
                .attr('cx', '150')
                .attr('cy', '100')
                .attr("r", '0')
                .attr("fill", (d, i) => color(i))
                .transition()
                .delay((d, i) => 500 * (3 - i))
                .duration(2000)
                .attr('r', (d, i) => d[1])



            const label = svg.append('g');
            label.selectAll('text').data(data).enter().append('text')

                .attr('stroke', (d, i) => color(i))
                .attr('transform', (d, i) => 'translate(170,80)' + `translate(80,${50-(i*10)})`)
                .style('font-size', '12px')

                .text(d => d[0])
        }
    }

</script>

<style>
    .state1 {
        fill: #ccc;
        stroke: #fff;
    }

    .state2 {
        fill: steelblue;
        stroke: #fff;
    }

</style>
