<script>
    export default {
        data() {
            return {
                player: null,
                ishover: false
            }
        },
        methods: {
            enter(mes) {
                console.log(mes)
                this.player = new Audio(`../../sound/${mes}.mp3`)
                this.player.play()
            },
            leave() {
                this.player.pause()
                this.player = null
            },
            show() {
                this.ishover = true
            },
            ushow() {
                this.ishover = false
            },
        }
    }

</script>
