<template>
    <div class='group' @mouseover='show' @mouseout="ushow">
        <div class='bgroup'>
            <svg class='b'>
                <use xlink:href="#i-conductor" />
            </svg>

        </div>

    </div>
</template>
<script>
    export default {
        data() {
            return {
                player: null,
                ishover: false
            }
        },
        methods: {
            enter(mes) {
                console.log(mes)
                this.player = new Audio(`../../sound/${mes}.mp3`)
                this.player.play()
            },
            leave() {
                this.player.pause()
                this.player = null
            },
            show() {
                this.ishover = true
            },
            ushow() {
                this.ishover = false
            },
        }
    }

</script>
<style scoped="true">
    .group {
        text-align: center
    }

    .label {
        padding: 0;
        margin: 0;
    }

    .b {
        position: relative;
        top: 15px;
        width: 50px;
        height: 50px;
        padding: 0;
        margin: 0;
    }

    .b:hover {
        fill: #42b983;
    }

    .bgroup {
        width: 200px;

        height: 80px;
        margin: 0px;

        background: #58a;

        background: linear-gradient(-120deg, transparent 45px, #6b6a69 0) right,
            linear-gradient(120deg, transparent 45px, #6b6a69 0) left;

        background-size: 60% 100%;

        background-repeat: no-repeat;

    }

    /*
    .bgroup:hover {

        background-color: burlywood
    }
*/

</style>
